import { Injectable } from '@angular/core';

import { SideBarItem } from '../model/sidebar-item';

const SIDEBAR_ITEMS: SideBarItem[] = [
    {name: 'gioithieu', value: 'Gii thiu', linkRef: '/admin/gioithieu', iconClass: 'fa-info'},
    {name: 'header', value: 'Header', linkRef: '/admin/header', iconClass: 'fa-header'},
    {name: 'dichvu', value: 'Dch v', linkRef: '/admin/dichvu', iconClass: 'fa-tachometer'},
    {name: 'khuyenmai', value: 'Khuyn mi', linkRef: '/admin/khuyenmai', iconClass: 'fa-tachometer'},
    {name: 'tintuc', value: 'Tin tc', linkRef: '/admin/tintuc', iconClass: 'fa-tachometer'},
    {name: 'bando', value: 'Bn ', linkRef: '/admin/bando', iconClass: 'fa-tachometer'},
    {name: 'thanhtoan', value: 'Thanh ton', linkRef: '/admin/thanhtoan', iconClass: 'fa-tachometer'},
    {name: 'danhmuc', value: 'Danh mc', linkRef: '/admin/danhmuc', iconClass: 'fa-tachometer'},
    {name: 'sanpham', value: 'Sn phm', linkRef: '/admin/sanpham', iconClass: 'fa-tachometer'},
    {name: 'gallery', value: 'Gallery', linkRef: '/admin/gallery', iconClass: 'fa-tachometer'},
];

@Injectable()
export class SideBarItemService {
    constructor() {}

    getSidebarItems(): SideBarItem[] {
        return SIDEBAR_ITEMS;
    }

    getItemByName(name: string): SideBarItem {
        let result = this.getSidebarItems().filter(item => {
            return (item.name === name);
        });
        return result? result[0] : null;
    }
}


